import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);
import login from '@/page/login'
import list from '@/page/interfaceList';

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '(\.*)/api-docs-login.html',
      name: 'api-docsr-login',
      component: login
    },
    {
      path: '(\.*)/api-docs.html',
      name: 'api-docs',
      component: list
    },
    {
      path: '/',
      name: 'api-docs',
      redirect: '/api-docs.html'
    }
  ]
});




